  .player video,
  .player iframe {
    margin: 0 auto;
    width: 64rem;
    height: 36rem; }
  .player .close {
    color: #fff;
    font-size: 6rem;
    position: absolute;
    right: 12rem;
    top: 6rem;
    cursor: pointer; }

.player.shown {
  top: 0;
  opacity: 1;
  transition: .25s ease-out; }

.player.hidden {
  top: 100%;
